.transition {
    -webkit-transform: scale(1.2); 
    -moz-transform: scale(1.2);
    -o-transform: scale(1.2);
    transform: scale(1.2);
}
.api-img {
	-webkit-transition: all .4s ease-in-out;
	-moz-transition: all .4s ease-in-out;
	-o-transition: all .4s ease-in-out;
    -ms-transition: all .4s ease-in-out;
    transition: all .4 ease-in-out;
}
tbody {
    border: unset;
}
td {
    background: white;
    padding: 20px !important;
    width: auto;
    height: auto;
    img {
        width: 100%;
        // height: 100%;
    }
}

.seated-icon-column {
    display: flex;
    width: 75%;
    font-family: lamborghini;
    align-items: center;
    font-size: 70px;
    justify-content: space-evenly;
    p {
        line-height: normal;
        font-weight: bolder;
        min-width: 160px;
    }
    .seated-app-icon-img {
        display: flex;
        width: 200px;
        height: 200px;
    }
}
.seated-feed-column {
    width: 25%;
}
@media (max-width: 900px) {
    .seated-icon-column {
        font-size: 40px;
    }
}
@media(max-width: 650px) {
    .seated-icon-column {
        width: 100%;
        font-size: 30px;
    }
    .seated-feed-column {
        width: 100%;
    }
}